/* Header Styles */
header {
    /* padding: 15px 20px; */
    width: 100%;
    /* height: 100px; */
    min-height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    position: fixed;
    top: 0;
    z-index: 1000;
    background-color: whitesmoke;
    flex-wrap: wrap;
  }
  
  header h1 {
    font-size: 24px;
    margin: 0;
    color: #181819;
    white-space: nowrap;
    margin-left: 100px;
  }
  
  header nav {
    background-color: whitesmoke;
    display: flex;
    align-items: center;
  }
  
  header nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  
  header nav ul li {
    font-size: 16px;
    margin: 0 10px;
  }
  
  header nav ul li a {
    color: #7C7C7D;
    text-decoration: none;
    padding: 8px 12px;
    transition: color 0.3s ease;
    display: inline-block;
    white-space: nowrap;
  }
  
  header nav ul li a:hover {
    color: black;
    text-decoration: none;
  }
  
  /* Button Styles */
   .Logout {
    border: 1px solid gray;
    padding: 8px 15px;
    text-align: center;
    line-height: normal;
    border-radius: 7px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
    min-width: 80px;
    height: auto;
  }
  .Login,.Register,.Cart{
    border: 1px solid gray;
    border-radius: 8px;
    text-align: center;
    align-content: center;
  }
  
  .Register {
    min-width: 120px;
    
  }
  
  .Logout {
    cursor: pointer;
    background: none;
    color: grey;
  }
  
  .Logout:hover {
    color: black;
    border-color: black;
  }
  
  .user-info {
    color: grey;
    padding: 0 10px;
    white-space: nowrap;
  }
  
  /* Notification Styles */
  .notifications-link {
    position: relative;
    display: flex;
    align-items: center;
  }
  
  .notification-badge {
    position: absolute;
    top: -8px;
    right: -8px;
    background: red;
    color: white;
    border-radius: 50%;
    width: 18px;
    height: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.7em;
  }
  
  /* Mobile Styles */
  @media (max-width: 768px) {
    header {
      flex-direction: column;
      padding: 10px;
      height: auto;
    }
  
    header h1 {
      margin-bottom: 10px;
      margin-left: 0;
    }
  
    header nav {
      width: 100%;
    }
  
    header nav ul {
      flex-direction: column;
      width: 100%;
      gap: 8px;
    }
  
    header nav ul li {
      width: 100%;
      margin: 0;
      text-align: center;
    }
  
    .Login, .Cart, .Register, .Logout {
      width: 100%;
      margin: 5px 0;
      box-sizing: border-box;
    }
  
    .user-info {
      width: 100%;
      text-align: center;
      padding: 10px 0;
      border-bottom: 1px solid #ddd;
      margin-bottom: 5px;
    }
  }